export default {
  props: {
    images: {
      type: Array
    },
    recordFinish: {
      type: Boolean,
      value: false,
    },
  },
  methods: {
    handleUserChooseImage() {
      uni.chooseImage({
        count: 4 - this.images.length,
        success: (chooseImageRes) => {
          this.$emit("imagesUpLoad", chooseImageRes);
        }
      });
    }
  }
};
